import { createRoot } from 'react-dom/client';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, Stars } from '@react-three/drei';
import { Physics, useBox, usePlane } from '@react-three/cannon';// fizik sistemi :)
import "./styles.css";


function Box() {
	const [ref] = useBox(() => ({ mass: 1 })); //mass ile ktle belirttim
	return (
		<mesh ref={ref} position={[0, 0.5, 0]}>{/* X Y ve Z ekseninde deerler verdim. */}
			<boxBufferGeometry attach="geometry" />
			{/* kutu geometrisi ve zerindeki doku malzemesini oluturdum */}
			<meshLambertMaterial attach="material" color="lightGreen" />
			{/* oluturduum geo ve mat  messh'in child' olarak belirttim */}
		</mesh>
	);
}

function Zemin() {
	const [refZ ,asd] = usePlane(() => ({
		rotation: [-Math.PI / 2, 0, 0] //zeminin deerini yazdm ki nesne oraya dsn.
	}));
	return (
		<mesh onClick={() => {
			asd.velocity.set(0,2,0);
			// her tklanma olaynda zemine den nesne 2 birim zplar.
		}}
			ref={refZ} rotation={[-Math.PI / 2, 0, 0]}>
			{/*  rotation={[-Math.PI / 2, 0, 0]} yazmasaydm zemin dikey grnrd. */}
			<planeBufferGeometry attach={"geometry"} args={[20, 20]} />
			<meshLambertMaterial attach={"material"} color={"royalblue"} />
		</mesh>
	);
}

createRoot(document.getElementById('root')).render(
	// dosyay root id li elemente ekledim
	<Canvas>
		<OrbitControls />
		{/* OrbitControls sayesinde mouse ile 3D evrende hareket ederim  */}
		<Stars />
		<ambientLight intensity={0.5} />{/* ambiyans  ekledim */}
		<spotLight
			position={[10, 15, 10]}
			angle={0.3}
		/>
		<Physics>
			<Box />
			<Zemin />
		</Physics>
	</Canvas>
	// 3D Evren canvas etiketi ierisinde alacaktr. 
);